.popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: 1s ease;
  position: fixed;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  width:100%;
  min-height:100%;
  margin: 0;
  padding: 0;
}

